.dialogue__background {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, #242a40 0%, rgba(126, 139, 184, 0) 90.42%);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}
.dialogue__main {
  display: flex;
  flex-direction: column;
  width: 60.18518518518518vh;
  margin-bottom: 3.148148148148148vh;
}
.dialogue__name {
  font-family: 'RobotoBold';
  font-size: 3.333333333333333vh;
  color: #1c2031;
}
.dialogue__text {
  font-family: 'RobotoMedium';
  font-size: 1.6666666666666665vh;
  color: #fff;
  margin-top: 1.2037037037037037vh;
}
.dialogue__answer {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 29.629629629629626vh);
  grid-gap: 0.9259259259259258vh;
  margin-top: 2.4074074074074074vh;
}
.dialogue__answer_box {
  width: 29.629629629629626vh;
  height: 5vh;
  background: rgba(26, 31, 48, 0.8);
  border-radius: 0.4629629629629629vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
}
.dialogue__answer_box:hover {
  background: linear-gradient(180deg, #23e0ed 0%, #00a3b5 100%);
  box-shadow: inset 0vh 0vh 2.222222222222222vh #00a3b5;
}
.dialogue__answer_box:hover .dialogue__answer_blur {
  opacity: 0.5;
}
.dialogue__answer_box:hover .dialogue__answer_text {
  color: #156169;
}
.dialogue__answer_blur {
  position: absolute;
  width: 17.84444444444444vh;
  height: 9.25925925925926vh;
  margin-top: -12.037037037037036vh;
  opacity: 0;
  filter: blur(7.4074074074074066vh);
  background: linear-gradient(180deg, #ffffff 0%, #ffffff 100%);
  box-shadow: inset 0vh 0vh 2.222222222222222vh #ffffff;
}
.dialogue__answer_text {
  z-index: 1;
  font-family: 'RobotoBold';
  font-size: 1.3888888888888888vh;
  color: #fff;
}

@media (aspect-ratio: 577/540) {
  /* 1154 x 1080 */
  .dialogue__main {
    transform: scale(0.8) translate(0vh, 3.7037037037037033vh);
  }
}
