.car__background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  background: linear-gradient(0deg, #242a40 -3.24%, rgba(70, 80, 119, 0) 54.72%);
}
.car__menu_box {
  width: 34.53703703703704vh;
  height: 58.05555555555555vh;
  background: #1a1f30;
  border-radius: 1.8518518518518516vh;
  position: relative;
  padding-top: 2.5925925925925926vh;
  padding-left: 0.9259259259259258vh;
  padding-right: 3.4259259259259256vh;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 7.499999999999999vh;
}
.car__menu_title {
  font-family: 'RobotoRegular';
  font-size: 2.314814814814815vh;
  color: #fff;
  margin-left: 4.444444444444444vh;
}
.car__menu_close {
  width: 2.1296296296296293vh;
  height: 2.1296296296296293vh;
  background: url(../img/icons/close.svg) no-repeat;
  background-size: cover;
  position: absolute;
  top: 2.8703703703703702vh;
  right: 2.5925925925925926vh;
}
.car__menu_list {
  width: 32.03703703703704vh;
  height: 50vh;
  margin-top: 2.7777777777777777vh;
  padding-left: 2.0370370370370368vh;
  overflow-y: scroll;
  direction: rtl;
}
.car__menu_list::-webkit-scrollbar {
  width: 0.4629629629629629vh;
  /* background: #514c60; */
  direction: rtl;
  border-radius: 0.27777777777777773vh;
}
.car__menu_list::-webkit-scrollbar-thumb {
  width: 0.4629629629629629vh;
  background: #514c60;
  border-radius: 0.27777777777777773vh;
  direction: rtl;
}
.car__menu_ul_box {
  display: flex;
  direction: ltr;
  width: 32.03703703703704vh;
}
.car__menu_ul {
  width: 100%;
  height: 100%;
  padding-top: 1.9444444444444444vh;
  padding-bottom: 1.9444444444444444vh;
  padding-left: 2.685185185185185vh;
  padding-right: 2.685185185185185vh;
  font-family: 'RobotoMedium';
  color: #fff;
  font-size: 1.8518518518518516vh;
  border-radius: 0.4629629629629629vh;
  position: relative;
}
.car__menu_ul_box input:checked + .car__menu_ul {
  background: linear-gradient(180deg, #23e0ed 0%, #00a3b5 100%);
  box-shadow: inset 0vh 0vh 2.222222222222222vh #00a3b5;
  background-size: cover;
  color: #156169;
}
.car__menu_ul_box input:checked + .car__menu_ul .car__menu_ul_blur {
  opacity: 1;
}
.car__menu_ul_blur {
  width: 100%;
  height: 100%;
  background: url(../img/icons/blur.svg) no-repeat;
  background-size: cover;
  background-position: center;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}
.car__menu_ul_text {
  font-family: 'RobotoRegular';
  font-size: 1.6666666666666665vh;
  color: #f36b6b;
  margin-left: 1.6666666666666665vh;
}
.car__range {
  width: 29.259259259259256vh;
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 50%;
  bottom: 3.888888888888889vh;
  transform: translate(-50%, 0);
}
.car__range_title {
  font-family: 'RobotoRegular';
  font-size: 2.314814814814815vh;
  color: #fff;
  margin-bottom: 1.3888888888888888vh;
}
.car__range_box {
  width: 100%;
  position: relative;
}
.car__range_box input[type='range'] {
  -webkit-appearance: none;
  width: 28.98148148148148vh;
  height: 0.18518518518518517vh;
  background: rgba(255, 255, 255, 0.5);
}
.car__range_box input[type='range']::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 1.8518518518518516vh;
  height: 1.8518518518518516vh;
  border-radius: 50%;
  background: #00a3b5;
  margin-top: -0.7407407407407407vh;
}
.car__range_box input[type='range']::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  width: 100%;
  height: 0.18518518518518517vh;
  border-radius: 0.18518518518518517vh 0vh;
}
.car__range_number {
  font-family: 'RobotoRegular';
  font-size: 1.3888888888888888vh;
  color: #fff;
  position: absolute;
  right: 0;
  bottom: 1.3888888888888888vh;
}
.car__range_riska1 {
  width: 0.18518518518518517vh;
  height: 0.7407407407407407vh;
  background: rgba(255, 255, 255, 0.5);
  position: absolute;
  bottom: 32%;
  left: 9.074074074074073vh;
}
.car__range_riska2 {
  width: 0.18518518518518517vh;
  height: 0.7407407407407407vh;
  background: rgba(255, 255, 255, 0.5);
  position: absolute;
  bottom: 32%;
  right: 7.87037037037037vh;
}
.car__button {
  width: 34.72222222222222vh;
  height: 7.592592592592592vh;
  margin-top: auto;
  margin-left: auto;
  margin-right: 7.499999999999999vh;
  margin-bottom: 4.62962962962963vh;
  background: linear-gradient(180deg, #23e0ed 0%, #00a3b5 100%);
  box-shadow: inset 0vh 0vh 2.222222222222222vh #00a3b5;
  border-radius: 0.4629629629629629vh;
  position: relative;
  font-family: 'RobotoMedium';
  font-size: 2.314814814814815vh;
  color: #156169;
  display: flex;
  align-items: center;
  justify-content: center;
}
.car__button:hover .car__button_blur {
  opacity: 1;
}
.car__button_blur {
  width: 100%;
  height: 100%;
  background: url(../img/icons/blur.svg) no-repeat;
  background-position: center;
  background-size: cover;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}
@media (aspect-ratio: 577/540) {
  /* 1154 x 1080 */
  .car__menu_box {
    transform: scale(0.68) translate(0, 0);
    transform-origin: left;
  }
  .car__range {
    transform: scale(0.68) translate(-50%, 0);
    transform-origin: left;
  }
  .car__button {
    transform: scale(0.65) translate(0, 0);
    transform-origin: right;
  }
}
@media (aspect-ratio: 4/3), (aspect-ratio: 5/4) {
  .car__menu_box {
    transform: scale(0.8) translate(0vh, 0vh);
    transform-origin: left;
  }
  .car__range {
    transform: scale(0.8) translate(-50%, 0);
    transform-origin: left;
  }
  .car__button {
    transform: scale(0.8) translate(0, 0);
    transform-origin: right;
  }
}
