.auth__background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
}
.auth__background::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 26, 45, 0.9);
  background-image: url(../img/icons/highlights.svg);
  background-repeat: no-repeat;
  background-size: cover;
}
.auth__help_text {
  position: absolute;
  top: 1.8518518518518516vh;
  left: 1.574074074074074vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: 'RobotoMedium';
  font-size: 1.3888888888888888vh;
  color: #fff;
  text-transform: uppercase;
}
.auth_help_box {
  width: 2.5925925925925926vh;
  height: 1.8518518518518516vh;
  border-radius: 0.27777777777777773vh;
  color: #525971;
  background: #fff;
  margin-left: 0.5555555555555555vh;
  margin-right: 0.5555555555555555vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.auth__main {
  width: 46.75925925925926vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
  margin-top: 6.666666666666666vh;
}
.auth__main_logo {
  width: 22.962962962962962vh;
  height: 9.351851851851851vh;
  background: url(../img/icons/logo.svg) no-repeat;
  background-size: cover;
}
.signin__main,
.signup__main {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.signin__title {
  margin-top: 13.61111111111111vh;
  font-family: 'RobotoMedium';
  font-size: 4.166666666666666vh;
  color: #fff;
}
.signin__title_text {
  font-family: 'RobotoRegular';
  font-size: 2.1296296296296293vh;
  color: #525971;
  margin-top: 1.8518518518518516vh;
  margin-bottom: 3.7037037037037033vh;
}
.signin__grid_box,
.signup__grid_box {
  display: grid;
  row-gap: 1.8518518518518516vh;
}
.signin__input_box,
.signup__input_box {
  width: 40.46296296296296vh;
  height: 3.796296296296296vh;
  background: #21273a;
  border-radius: 0.9259259259259258vh;
  padding-left: 3.518518518518518vh;
  padding-right: 2.5925925925925926vh;
  padding-top: 1.8518518518518516vh;
  padding-bottom: 1.8518518518518516vh;
  display: flex;
  align-items: center;
  border: 0.09259259259259259vh solid #fff0;
}
.signin__input,
.signup__input,
.signin__input::placeholder,
.signup__input::placeholder {
  width: 100%;
  font-family: 'RobotoRegular';
  font-size: 1.8518518518518516vh;
  color: #525971;
}
.signin__input_failed,
.signup__input_failed {
  border: 0.09259259259259259vh solid #f36b6b;
}
.signin__img_failed,
.signup__img_failed {
  width: 1.8518518518518516vh;
  height: 2.1296296296296293vh;
  background: url(../img/icons/warning.svg) no-repeat;
  background-size: cover;
  padding-right: 0.27777777777777773vh;
  z-index: 999;
}
.sigin__main_warning_box {
  font-family: 'RobotoRegular';
  font-size: 1.6666666666666665vh;
  color: #fff;
  position: relative;
}
.sigin__warning_box {
  z-index: 999;
  height: 2.7777777777777777vh;
  width: 2.7777777777777777vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.sigin__warning_box:hover ~ .sigin_box_popup_text .sigin_popup_text {
  opacity: 1;
}
.sigin_box_popup_text {
  width: 37.03703703703704vh;
  position: absolute;
  top: 50%;
  transform: translate(0%, -50%);
  left: 4.814814814814815vh;
}
.sigin_popup_text {
  width: fit-content;
  max-width: 37.03703703703704vh;
  background: #eb6d6f;
  border-radius: 0.9259259259259258vh;
  padding-top: 1.2037037037037037vh;
  padding-bottom: 1.2037037037037037vh;
  padding-left: 1.4814814814814814vh;
  padding-right: 1.4814814814814814vh;
  transition: all 0.5s;
  opacity: 0;
}
.sigin_popup_text::before {
  content: '';
  width: 1.6666666666666665vh;
  height: 0vh;
  border: 0.7407407407407407vh solid transparent;
  border-right: 1.0185185185185184vh solid #eb6d6f;
  margin-left: -4.814814814814815vh;
  position: absolute;
  top: 50%;
  transform: translate(0%, -50%);
}
.signin__input_visibility,
.signup__input_visibility {
  width: 2.314814814814815vh;
  height: 2.1296296296296293vh;
  background-repeat: no-repeat;
  background-size: cover;
  margin-left: 0.9259259259259258vh;
}
.signin__text_right {
  margin-left: auto;
  margin-top: 1.8518518518518516vh;
  font-family: 'RobotoRegular';
  font-size: 1.8518518518518516vh;
  color: #525971;
  border-bottom: 0.09259259259259259vh solid #fff0;
}
.signin__text_right:hover {
  border-bottom: 0.09259259259259259vh solid #525971;
}
.signin__button,
.signup__button {
  width: 100%;
  height: 7.4074074074074066vh;
  background: linear-gradient(180deg, #23e0ed 0%, #00a3b5 100%);
  box-shadow: inset 0vh 0vh 2.222222222222222vh #00a3b5;
  border-radius: 0.4629629629629629vh;
  margin-top: 1.8518518518518516vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.signin__button_blur,
.signup__button_blur {
  width: 100%;
  height: 100%;
  background: url(../img/icons/blur.svg) no-repeat;
  background-size: cover;
  opacity: 0;
  position: absolute;
  transition: all 1s;
}
.signin__button:hover .signin__button_blur,
.signup__button:hover .signup__button_blur {
  opacity: 1;
}
.signin__button_text,
.signup__button_text {
  font-family: 'RobotoBold';
  font-size: 2.0370370370370368vh;
  color: #156169;
  text-transform: uppercase;
}
.signin__text_signup,
.signup__text_signin {
  font-family: 'RobotoRegular';
  font-size: 1.8518518518518516vh;
  color: #525971;
  margin-top: 4.444444444444444vh;
  border-bottom: 0.09259259259259259vh solid #525971;
}
.signin__text_signup:hover,
.signup__text_signin:hover {
  border-bottom: hidden;
}

.signup__title {
  margin-top: 8.055555555555555vh;
  font-family: 'RobotoMedium';
  font-size: 4.166666666666666vh;
  color: #fff;
}
.signup__title_text {
  width: 58.7037037037037vh;
  text-align: center;
  font-family: 'RobotoRegular';
  font-size: 2.1296296296296293vh;
  color: #525971;
  margin-top: 1.8518518518518516vh;
  margin-bottom: 3.7037037037037033vh;
}
.reset__title {
  width: 49.44444444444444vh;
  margin-top: 18.24074074074074vh;
  font-family: 'RobotoMedium';
  font-size: 4.166666666666666vh;
  color: #fff;
}
.reset_popup {
  width: 100%;
  height: 10.37037037037037vh;
  background: #3aad61;
  border-radius: 0.9259259259259258vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.8518518518518516vh;
  text-align: center;
  font-family: 'RobotoRegular';
  font-size: 1.8518518518518516vh;
  color: #fff;
}
