.wshop__position {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.wshop__main {
  width: 87.5vh;
  height: 64.9074074074074vh;
  background: rgba(26, 31, 48, 0.9);
  border-radius: 1.8518518518518516vh;
  padding: 3.7037037037037033vh 1.8518518518518516vh 2.5925925925925926vh 3.7037037037037033vh;
  overflow: hidden;
  position: relative;
  z-index: 1;
}
.wshop_blur {
}
.wshop_blur::before {
  content: '';
  width: 52.96296296296296vh;
  height: 52.96296296296296vh;
  border-radius: 50%;
  background: #373d53;
  opacity: 0.6;
  filter: blur(23.148148148148145vh);
  position: absolute;
  top: -21.296296296296294vh;
  left: -22.314814814814813vh;
  z-index: -1;
}
.wshop_blur::after {
  content: '';
  width: 52.96296296296296vh;
  height: 52.96296296296296vh;
  background: #00a3b5;
  opacity: 0.4;
  filter: blur(23.148148148148145vh);
  position: absolute;
  bottom: -39.25925925925926vh;
  right: -20.648148148148145vh;
  z-index: -1;
}
.wshop__box_title {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.wshop__box_title_text {
  font-family: 'RobotoMedium';
  font-size: 3.7037037037037033vh;
  color: #fff;
}
.wshop__box_balance {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-left: 21.75925925925926vh;
  margin-right: 8.61111111111111vh;
}
.wshop__box_balance_text_grey {
  font-family: 'RobotoRegular';
  font-size: 1.574074074074074vh;
  color: rgba(255, 255, 255, 0.2);
  margin-bottom: 0.7407407407407407vh;
}
.wshop__box_balance_text_white {
  font-family: 'RobotoRegular';
  font-size: 1.8518518518518516vh;
  color: #fff;
}
.wshop__box_title_close {
  width: 2.1296296296296293vh;
  height: 2.1296296296296293vh;
  background: url(../img/icons/close.svg) no-repeat;
  background-size: cover;
  margin-right: 1.8518518518518516vh;
  margin-left: auto;
}
.wshop__category {
  margin-top: 3.6111111111111107vh;
  width: 85.83333333333333vh;
  height: 5.185185185185185vh;
  display: flex;
  flex-direction: row;
  border-bottom: 0.09259259259259259vh solid rgb(255 255 255 / 20%);
}
.wshop__category_button:last-child {
  margin-right: 0vh;
}
.wshop__category_button {
  margin-right: 3.888888888888889vh;
}
.wshop__category_button_text {
  font-family: 'RobotoRegular';
  font-size: 1.8518518518518516vh;
  color: rgba(255, 255, 255, 0.2);
}
.wshop__category_button input[type='radio']:checked + .wshop__category_button_text {
  color: #00a3b5;
}
.wshop__content {
  margin-top: 2.7777777777777777vh;
}
.wshop__content_title {
  font-family: 'RobotoRegular';
  font-size: 2.314814814814815vh;
  color: #fff;
}
.wshop__grid {
  margin-top: 1.574074074074074vh;
  display: grid;
  grid-template-columns: repeat(3, 26.759259259259256vh);
  gap: 2.7777777777777777vh;
  height: 44.07407407407407vh;
  overflow-y: auto;
}
.wshop__grid::-webkit-scrollbar {
  width: 0.4629629629629629vh;
  background: rgba(33, 39, 58, 0.6);
  border-radius: 1.3888888888888888vh;
}
.wshop__grid::-webkit-scrollbar-thumb {
  width: 0.4629629629629629vh;
  background: #00a3b5;
  border-radius: 1.3888888888888888vh;
}
.wshop_grid_box {
  width: 21.75925925925926vh;
  height: 17.87037037037037vh;
  background: rgba(33, 39, 58, 0.6);
  border-radius: 0.9259259259259258vh;
  z-index: 1;
  position: relative;
  padding-top: 1.3888888888888888vh;
  padding-bottom: 1.3888888888888888vh;
  padding-left: 2.5vh;
  padding-right: 2.5vh;
}
.wshop_grid_box_blur {
  width: 9.62962962962963vh;
  height: 9.62962962962963vh;
  background: #373d53;
  filter: blur(3.7037037037037033vh);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
}
.wshop_grid_box_title {
  display: flex;
  flex-direction: row;
}
.wshop_grid_box_title_right_top {
  display: flex;
}
.wshop_grid_box_title_left_text_grey,
.wshop_grid_box_title_right_text_grey {
  font-family: 'RobotoRegular';
  font-size: 0.9259259259259258vh;
  color: rgba(255, 255, 255, 0.2);
}
.wshop_grid_box_title_left_text_white,
.wshop_grid_box_title_right_text_white {
  font-family: 'RobotoRegular';
  font-size: 1.8518518518518516vh;
  color: #fff;
}
.wshop_grid_box_title_left_text_white {
  margin-top: 0.6481481481481481vh;
  width: 13.425925925925926vh;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.wshop_grid_box_title_right_text_white {
  margin-left: auto;
  margin-top: -0.5555555555555555vh;
}
.wshop_grid_box_title_right {
  margin-left: auto;
}
.wshop_grid_box_title_right_bar {
  width: 7.87037037037037vh;
  height: 0.4629629629629629vh;
  background: #343a4f;
  border-radius: 9.25925925925926vh;
  margin-top: 0.5555555555555555vh;
}
.wshop_grid_box_title_right_line {
  height: 0.4629629629629629vh;
  background: #00a3b5;
  border-radius: 9.25925925925926vh;
}
.wshop__grid_img {
  width: auto;
  height: 8.61111111111111vh;
  background-repeat: no-repeat;
  /* background-size: cover; */
  background-size: contain;
  background-position: center;
  margin-top: 0.18518518518518517vh;
  margin-left: auto;
  margin-right: auto;
}
.wshop__grid_row {
  display: flex;
  flex-direction: row;
  margin-top: 0.6481481481481481vh;
}
.wshop__grid_row_box_price {
  width: 9.351851851851851vh;
  height: 3.6111111111111107vh;
  background: #525971;
  border-radius: 0.4629629629629629vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'RobotoRegular';
  font-size: 1.6666666666666665vh;
  color: #fff;
}
.wshop__grid_row_box_buy {
  width: 9.351851851851851vh;
  height: 3.6111111111111107vh;
  background: linear-gradient(180deg, #23e0ed 0%, #00a3b5 100%);
  box-shadow: inset 0vh 0vh 2.222222222222222vh #00a3b5;
  border-radius: 0.4629629629629629vh;
  font-family: 'RobotoMedium';
  font-size: 1.6666666666666665vh;
  color: #156169;
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.wshop__grid_row_box_buy::before {
  content: '';
  width: 4.463888888888889vh;
  height: 5.152777777777778vh;
  border-radius: 50%;
  filter: blur(1.8518518518518516vh);
  background: rgba(255, 255, 255, 0.5);
  position: absolute;
  top: -2.8703703703703702vh;
  left: 50%;
  transform: translate(-50%, 0%);
  z-index: -1;
}
@media (aspect-ratio: 577/540) {
  /* 1154 x 1080 */
  .wshop__main {
    transform: scale(0.85) translate(0, 0);
  }
}
