  }
  li {
    list-style-type: none;
  }
  body {
    -webkit-user-select: none;
    margin: 0 auto;
  }
  input {
    background: none;
    outline: none;
    border: none;
  }
  textarea {
    background: transparent;
    border: none;
    outline: none;
  }
  input[type='radio'],
  input[type='checkbox'] {
    display: none;
  }
  ::-webkit-scrollbar {
    width: 0vh;
    background: transparent;
  }
  