/*alert*/
.alert-enter {
  opacity: 0;
  transform: scale(0.9);
}
.alert-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 600ms, transform 600ms;
}
.alert-exit {
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 600ms, transform 600ms;
}
/*fade*/
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@-webkit-keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.fade-enter-active {
  opacity: 1;
  animation: fadeIn 4s;
}

.fade-enter {
  opacity: 0;
}

.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  animation: fadeOut 0.4s;
}
/*fade quest*/

@-webkit-keyframes fadeOutRight {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
  }
}
@keyframes fadeOutRight {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
  }
}

.fade_quest-enter-active {
  opacity: 1;
  animation: fadeIn 1s;
}

.fade_quest-enter {
  opacity: 0;
}
.fade_quest-exit {
  opacity: 1;
}
.fade_quest-exit-active {
  opacity: 0;
  animation: fadeOutRight 1s;
}

/*fade_zone*/
.fade_zone-enter-active {
  opacity: 1;
  animation: fadeIn 1s;
}

.fade_zone-enter {
  opacity: 0;
}
.fade_zone-exit {
  opacity: 1;
}
.fade_zone-exit-active {
  opacity: 0;
  animation: fadeOut 1s;
}
