@media (aspect-ratio: 577/540) {
  /* 1154 x 1080 */
  .hud_capt_box {
    transform: scale(0.65) translate(0, 0);
  }
  .hud__capt_timer_box {
    transform: scale(0.7) translate(0, 0);
  }
}
@media (aspect-ratio: 4/3), (aspect-ratio: 5/4) {
