.notifi_bottom_center {
  position: fixed;
  bottom: 2.5vh;
  left: 50%;
  transform: translateX(-50%);
}
.notifi_container {
  z-index: 9999;
  position: fixed;
  transition: all 1s;
}

.notifi__main {
  width: 55.37037037037037vh;
  background: linear-gradient(90deg, rgba(33, 39, 58, 0.4465) 41.95%, rgba(0, 163, 181, 0.282) 96.88%), rgba(33, 39, 58, 0.9);
  border-radius: 0.9259259259259258vh;
  display: flex;
  margin-top: 1.4814814814814814vh;
}
.notifi__left_box {
  width: 7.499999999999999vh;
  height: auto;
  background: #00a3b5;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom-left-radius: 0.9259259259259258vh;
  border-top-left-radius: 0.9259259259259258vh;
}
.notifi__left_box_img {
  width: 3.148148148148148vh;
  height: 3.148148148148148vh;
  background-repeat: no-repeat;
  background-size: cover;
}
.notifi__text_box {
  width: 40.27777777777778vh;
  padding-top: 2.4074074074074074vh;
  padding-left: 4.814814814814815vh;
  padding-right: 1.3888888888888888vh;
  padding-bottom: 2.5vh;
  font-family: 'RobotoRegular';
  font-size: 1.8518518518518516vh;
  color: #fff;
}
.notifi__timer_box {
  width: 0.5555555555555555vh;
  height: auto;
  background: #156169;
  border-radius: 1.3888888888888888vh;
  margin-top: 1.2037037037037037vh;
  margin-bottom: 1.2037037037037037vh;
  margin-right: 1.2037037037037037vh;
  margin-left: auto;
}
@keyframes notifiprogress {
  from {
    height: 0%;
  }
  to {
    height: 100%;
  }
}
.notifi__timer_line {
  width: 100%;
  height: 0%;
  background: #00a3b5;
  border-radius: 1.3888888888888888vh;
  animation: notifiprogress;
  animation-iteration-count: 1;
  animation-timing-function: linear;
  animation-duration: var(--duration);
  animation-fill-mode: forwards;
}

.test_button {
  width: 4.62962962962963vh;
  height: 4.62962962962963vh;
  background: antiquewhite;
  position: absolute;
  z-index: 999;
}
@media (aspect-ratio: 4/3), (aspect-ratio: 5/4), (aspect-ratio: 577/540), (aspect-ratio: 577/540) {
  .notifi__main {
    width: 31.145833333333325vw;
  }
  .notifi__left_box {
    width: 4.218749999999999vw;
  }
  .notifi__text_box {
    width: 22.65625vw;
    padding-left: 2.7083333333333335vw;
    padding-right: 0.78125vw;
    font-size: 1.0416666666666665vw;
  }
  .notifi__timer_box {
    width: 0.31249999999999994vw;
    margin-right: 0.6770833333333334vw;
  }
  .notifi__left_box_img {
    width: 1.7708333333333333vw;
    height: 1.7708333333333333vw;
  }
}
